body{
    margin:0; 
    padding:100px 0px 0px 0px;
    background-color:hsl(219, 23%, 17%);
    color:hsl(217, 19%, 38%);
    font-family: 'Manrope', sans-serif;
    font-family: 'Poppins', sans-serif;
    font-family: 'Public Sans', sans-serif;
    font-weight: 800;

}

.container{
    width: 40%;
    margin: 0 auto;
    text-align: center;
    background-color: hsl(217, 19%, 24%);
    padding:20px 30px;
    border-radius:10px;

}
h1{
    font-size: 10px;
    color:hsl(150, 100%, 66%);
    letter-spacing: 3px;
}
p{
    font-size: 28px;
    color:hsl(193, 38%, 86%);
}
.pattern{
    width: 100%;
    margin: 0 auto;
}
.dice{
    background-color: hsl(150, 100%, 66%);
    border-radius: 50%;
    padding:8px;
    margin-bottom: -45px;
}
.dice:hover{
    border: 1px solid;
    padding: 10px;
    box-shadow:  0 0 50px hsl(150, 100%, 66%),            /* outer white */
    -6px 0 80px hsl(150, 100%, 66%),        /* outer left magenta */
    6px 0 80px hsl(150, 100%, 66%);
}


@media only screen and (max-width: 900px){
    p{
        font-size: 15px;
        color:hsl(193, 38%, 86%);
    }
    .container{
        width: 60%;
    
    }
    body{
        padding:130px 0px 0px 0px;
      
    }
}
